.carrito {
  border: 1px solid #ccc;
  padding: 20px;
  height: auto;
}

.lista-carrito {
  list-style: none;
  padding: 0;
}

.lista-carrito li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.vaciar-carrito {
  background-color: #ff0000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 5px;
}

.vaciar-carrito:hover {
  background-color: #cc0000;
}
.eliminar-producto {
  background-color: red;
  border-radius: 5px;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
}

.eliminar-producto:hover {
  background-color: darkred;
}
#totalCarrito {
  text-align: right;
  font-weight: bold;
  margin-top: 10px;
}

#botonPagar {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  float: left;
  margin-right: 2rem;
  border-radius: 5px;
}

#botonPagar:hover {
  background-color: #45a049;
}
